import * as React from 'react';
import { Component } from 'react';

import './navbar.scss';

interface INavbar {
  homePage?: boolean;
}

export class Navbar extends Component<INavbar, any> {
  constructor(props: INavbar){
    super(props);
  }

  render() {
    return (
      <div className="navbar">
        <span className={"logo " + (this.props.homePage ? 'navbar__logo--centered' : null)}>POSHMARK</span>
      </div>
    );
  }
}
