import * as React from 'react';
import { Component } from 'react';

import { Navbar } from '@posh/navbar';
import { Hero } from '@posh/hero';
import { SectionHeader } from '@posh/section-header';
import { Pill } from '@posh/pill';

import './home-page.scss';

interface IPill {
  title: string;
  active: boolean;
}



interface IHomePageState {
  pills: Array<IPill>
}

export class HomePage extends Component<any, IHomePageState> {
  constructor(props: any)  {
    super(props);

    this.state = {
      pills: [
        { title: 'Todo', active: true },
        { title: 'Mujeres', active: false },
        { title: 'Hombres', active: false },
      ]
    }

    this.pillClickHandler = this.pillClickHandler.bind(this);
  }

  pillClickHandler(clickedPill: IPill) {
    let pills = this.state.pills;
    pills = pills.map(pill => {
      pill.active = pill.title === clickedPill.title;
      return pill;
    })

    this.setState({pills})
  }

  render() {
    return (
      <div className="home-page">
        <Navbar homePage={true} />
        <Hero />
        <div className="home-page__content">
          <SectionHeader title="Las marcas de moda" />
          <div className="home-page__pills">
            {this.state.pills.map((pill,index) => {
              return (
                <span
                  key={index}
                  onClick={this.pillClickHandler.bind(this, pill)}>
                  <Pill
                    title={pill.title} 
                    active={pill.active}/>
                </span>
              );
            })}
          </div>
          <SectionHeader title="¿Como funciona?" />
        </div>
      </div>
    );
  }
}
